*{
  padding: 0;
  margin: 0;
}
.mycontainer{
  width: auto;
  margin: 0 auto;
  margin-left: 80px;
  margin-right: 80px;
}
@media (max-width: 1080px){
  .mycontainer {
    margin: 0 auto;
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }
}
///////////////////////////HEADER
.header{
  height: 55px;
  background-color: black;
  position: fixed;
  width: 100%;
  opacity: 0.9;
  z-index: 999;
  top: 0;
}

.menu{
  display: flex;
  justify-content: center;
  height: 55px;
  transition: 0.5s;
}
.nav_list{
  display: flex;
  flex-direction: row;
  list-style-type: none;
}
.logo{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 50px;
}
.logo>span{
  font-size: 20px;
  color: white;
  font-weight: bold;
}
.nav_list_items{
  margin-top: 8px;
}
.nav_list_items a{
  text-decoration: none;
}
.nav_item{
  display: inline-block;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  height: 35px;
  width: 200px;
  background-color: black;
  color: white;
  margin-left: 8px;
  margin-right: 8px;
  margin-top: 2px;
  transition: 0.5s;
  font-weight: 700;
  letter-spacing: 1px;
  font-size: 18px;

}
.nav_item:hover{
  background: #800353;
  transform: scale(1);
  transition: 0.3s;
  color: white;
  font-weight: 700;
  letter-spacing: 1px;
  text-decoration: none;
}
.menu-toggle{
  background-color: red;
  width: 120px;
  height: 120px;
  display: flex;
  background: none;
  border:0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  z-index: 1;
  position: absolute;
}
.toggle{
  background: white;
  display: block;
  height: 12px;
  width: 70px;
  margin: 5px;
}
@media (min-width: 1080px) {
  .menu-toggle{
    display: none;
  }
}
@media (max-width: 1080px) {
  .menu {
    transition: 0.5s;
    position: absolute;
    top: 0;
    left:-700px;
    width: 700px;
    background: black;
    margin: 0;
    height: 100vh;
    justify-content: center;
    .nav_list{
      flex-direction: column;
      margin-top: 100px;
      margin-left: 20px;
      align-items: center;
      .nav_list_items{
        text-align: left;
        font-size: 50px;
        margin-top: 40px;
        margin-bottom: 20px;
        .nav_item{
          height: 90px;
          width: 500px;
          font-weight: 500;
          margin-right:0;
          margin-left:0;
          font-size: 50px;
        }
      }
    }
  }
  .menu-toggle{
    z-index: 99;
  }
  .menu.show{
    transform: translate(100%);
  }
  .header{
    height: 120px;
    width: 100%;
  }
}
@media (max-width: 1080px) {
  .nav_item{
    height: 30px;
    width: 150px;
    font-weight: 500;
    margin-right: 6px;
    margin-left: 3px;
    font-size: 15px;
  }
  .parallax-window{
    background-size: cover;

  }
}

////////////////HOME

#section-one{
  width: 100%;
  background-size: cover;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main_info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main_title{
display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 10px;
}
.main_title>span{
  font-size: 45px;
  letter-spacing: 3px;
  font-weight: 600;
}
.main_text{
  margin-top: 30px;
  letter-spacing: 1px;
  font-size: 25px;
  font-weight: 500;
  width: 50%;
  text-align: center;
}
.main_underline{
  background-color: purple;
  height: 4px;
  width: 60%;
}
.mainBtn{
  margin-top: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 60px;
  width: 200px;
  background-color: gold;
  cursor: pointer;
  border-radius: 8px;
  text-decoration: none;
  font-size: 23px;
  font-weight: 700;
  transition: 0.3s;
  color: #4d4d4d;
  border: 1px solid lightgrey;
}



.mainBtn:hover{
  transform: scale(1);
  transition: 0.3s;
  color: #4d4d4d;
  text-decoration: none;
  background-color: goldenrod;
}

@media (max-width: 1080px) {

  .main_title>span{
    font-size: 70px;
    letter-spacing: 3px;
    font-weight: 600;
  }
  .main_text{
    margin-top: 30px;
    letter-spacing: 1px;
    font-size: 40px;
    font-weight: 400;
    width: 65%;
    text-align: center;
  }
  .mainBtn{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100px;
    width: 320px;
    background-color: purple;
    cursor: pointer;
    border-radius: 0px;
    text-decoration: none;
    font-size: 40px;
    font-weight: 600;
    transition: 0.3s;
    color: whitesmoke;
    //border: 2px solid lightgrey;
  }
}



.parallax-window {
  min-height: 400px;
  background: transparent;
}
