///////////////////////Masaze

#section-four {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.description {
  width: 70%;
  margin: 30px auto;
  border: 1px solid purple;
}
.description dl{
  margin-bottom: 0;
}

.description dt {
  cursor: pointer;
  font-size: 21px;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 25px;
  letter-spacing: 1px;
  transition: 0.3s;
}
.description dt:hover{
 background-color: gainsboro;
  transition: 0.3s;
  div{
    color: purple;
  }
}

.description dd {
  font-size: 22px;
  color: #777;
  margin: 0;
  padding: 1.8em;

}
.offer_title{
  text-align: center;
  font-size: 35px;
  padding-top: 50px;
  letter-spacing: 4px;
  font-weight: 600;
}
.time, .price{
  font-size: 17px;
  color: dimgrey;
  font-weight: 500;
}


@media (max-width: 1080px) {
  #section-three{
    display: none;
  }
  .description {
    width: 95%;
    margin: 20px auto;
    border: 1px solid purple;
  }
  .description dt {
    cursor: pointer;
    font-size: 31px;
    height: 140px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding:18px;
    letter-spacing: 1px;
    transition: 0.3s;
  }
  .description dd {
    font-size: 33px;
    color: #777;
    margin: 0;
    padding: 1.6em;
  }
  .time, .price{
    font-size: 24px;
    color: dimgrey;
    font-weight: 500;
  }
  .offer_title{
    text-align: center;
    font-size: 50px;
    padding-top: 50px;
    letter-spacing: 4px;
    font-weight: 500;
  }
}
////////////////////////REVIEWS
#section-five{
  height: 400px;
  width: 100%;
  background-image: url("../images/h1-background-img.jpg");
  background-size: cover;
  position: relative;
}
.col-md-8{
  margin-top: 0;
}
#myCarousel{
  padding: 60px;
}
.col-md-8{
  margin-top: 0;
}
.col-center {
  margin: 0 auto;
  float: none !important;
  position: relative;
  margin-top: 60px;
}

.carousel .item {
  color: black;
  font-size: 14px;
  text-align: center;
  overflow: hidden;
  min-height: 290px;
}
.carousel .item .img-box {
  width: 90px;
  height: 90px;
  margin: 0 auto;
  margin-bottom: 13px;
  font-size: 30px;
}
.img-box{
  font-size: 30px;
}
.carousel .img-box img {
  width: 100%;
  height: 100%;

}
.carousel .testimonial {
  font-size: 20px;
  font-weight: 300;
}
.carousel .overview {
  font-style: italic;
}
.carousel .overview b {
  text-transform: uppercase;
  color: purple;
  font-size: 20px;
  text-decoration: none;
}

.carousel .carousel-indicators {
  bottom: -40px;
}
.carousel-indicators li, .carousel-indicators li.active {
  width: 10px;
  height: 10px;
  margin: 1px 3px;
  border-radius: 50%;
}
.carousel-indicators li {
  background: #999;
  border-color: transparent;
  box-shadow: inset 0 2px 1px rgba(0,0,0,0.2);
}
.carousel-indicators li.active {
  background: #555;
  box-shadow: inset 0 2px 1px rgba(0,0,0,0.2);
}

@media (max-width: 1080px) {
  #section-five{
    height: 700px;
    width: 100%;
  }
  .carousel .item {
    color: black;
    font-size: 20px;
    text-align: center;
    overflow: hidden;
    min-height: 290px;
  }
  .carousel .item .img-box {
    width: 110px;
    height: 110px;
    margin: 0 auto;
    margin-bottom: 10px;
  }
  .carousel .testimonial {
    font-size: 30px;
    font-weight: 300;
  }
  .carousel .carousel-indicators {
    bottom: -20px;
  }
}

