//////////////////////////social media

.fl-fl {
  background: darkblue;
  text-transform: uppercase;
  letter-spacing: 3px;
  width: 250px;
  position: fixed;
  right: -200px;
  z-index: 1000;
  font: normal normal 10px Arial;
  -webkit-transition: all .25s ease;
  -moz-transition: all .25s ease;
  -ms-transition: all .25s ease;
  -o-transition: all .25s ease;
  transition: all .25s ease;
}
.fa {
  font-size: 60px;
  color: #fff;
  padding: 12px 0;
  width: 60px;
  margin-left: 15px;
}
.fl-fl:hover {
  right: 0;
  text-decoration: none;
}
.fl-fl a {
  color: #fff ;
  text-decoration: none;
  text-align: center;
  line-height: 60px;
  vertical-align: top;
  font-weight: 700;
  font-size: 20px;

}
.float-fb {
  top: 100px;
}
.fa-facebook-f:before, .fa-facebook:before{
  font-size: 35px;
}
@media (max-width: 1080px) {
